import { FastifyInstance } from 'fastify';
import app from '../src/app';

export async function startTestEnv(): Promise<FastifyInstance> {
	const app = require('..//src/app').default;

	await app.ready();

	return app;
}

export async function stopTestEnv(): Promise<void> {
	await app.close();
}

